* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
  background-color: #21232a;
  color: white;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  background-color: #29232a;
  border: 1px solid gray;
  border-radius: 5px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  margin: 10px auto;
  overflow: hidden;
}

.card figcaption {
  padding: 25px;
  text-align: center;
}

.card img {
  display: block;
  margin: 0 auto;
  max-height: 170px;
}
